import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { TabsetComponent } from 'ngx-bootstrap/tabs';
import { SharedService } from '../../../../services/shared.service';
@Component({
  selector: 'app-return-treasury-fund',
  templateUrl: './return-treasury-fund.component.html',
  styleUrls: ['./return-treasury-fund.component.scss']
})
export class ReturnTreasuryFundComponent {
  data;
  canManageAndView;
  @ViewChild(TabsetComponent, { static: true }) tabSet: TabsetComponent;
  constructor(private _sharedService: SharedService,
    private router: Router
  ) { }
  ngOnInit() {
    let tab = JSON.parse(localStorage.getItem('returnParams'))?.tab;
    if (tab >= 0) {
      this.tabSet.tabs[tab].active = true;
      localStorage.setItem('returnParams', JSON.stringify({ ...JSON.parse(localStorage.getItem('returnParams')), ...{ tab } }))
    }
    this.getPermissions();
  }
  getPermissions() {
    this.canManageAndView = this._sharedService.getPermissions.find(x => x.permissionCatId == 29)
      .permissionSubCategories.find(y => y.permissionSubCatId == 74).permissionItems.find(z => z.permissionItemId == 169).selected;
    this.data = { branchTypeId: 3002, canManageAndView: this.canManageAndView };
  }
  handleTabSelection(event, branchTypeId, tab) {
    localStorage.removeItem('returnParams');
    this.data = { branchTypeId, canManageAndView: this.canManageAndView };
    localStorage.setItem('returnParams', JSON.stringify({ ...JSON.parse(localStorage.getItem('returnParams')), ...{ tab } }))
  }
  ngOnDestroy() {
    let nextRoute = (this.router.url);
    if (!nextRoute.includes('/return/details') && !nextRoute.includes('/return/services-details') && !nextRoute.includes('/return/dpayment-details')) {
      localStorage.removeItem('returnParams')
    }  
  }
}
